@use 'base/mixins' as m;
@use 'base/variables' as v;

.navegacion {
    display: flex;
    flex-direction: column;
    align-items: center;

    @include m.tablet {
        flex-direction: row;
        gap: 2rem;
    }

    &__enlace{
        margin-bottom: 2rem;
        font-size: 1.4rem;
        display: flex;
        align-items: center;

        &:last-of-type {
            margin: 0;
        }

        @include m.tablet {
            margin: 0;
        }

        &--crear {
            font-weight: 700;
            color: v.$primario;

            @include m.tablet { /* agregando linea divisora next to otros enlaces */
                &::after {
                    content: '';
                    display: inlineblock;
                    height: 4rem;
                    border-right: 2px solid v.$grisClaro;
                    margin: 0 0 0 2rem;
                }
            }
        }
    }

    
}